'use server';

import { cookies } from 'next/headers';
import { z } from 'zod';
import { redirect } from 'next/navigation';
import { serverAuthenticateArcGIS } from  '../lib/authenticateArcGIS';

export async function login(prevState: any, formData: FormData) {
  const schema = z.object({
    username: z.string().min(3),
    password: z.string().min(4),
  });

  const parsed = schema.safeParse(Object.fromEntries(formData));
  if (!parsed.success) {
    return {
      errors: parsed.error.flatten().fieldErrors,
    };
  }

  const { username, password } = parsed.data;

  const tokenData = await serverAuthenticateArcGIS(username, password);

  if (tokenData) {
    const cookieStore = await cookies();
    cookieStore.set('arcgis_token', tokenData.token, {
      httpOnly: true,
      secure: true,
      sameSite: 'lax',
      path: '/',
      expires: new Date(tokenData.expires),
    });

    // Optional readable username cookie
    cookieStore.set('arcgis_user', username, {
      httpOnly: false,
      secure: true,
      sameSite: 'lax',
      path: '/',
      expires: new Date(tokenData.expires),
    });

    redirect('http://localhost:3000');
  }

  return {
    errors: {
      username: ['Invalid username or password'],
    },
  };
}
